import {Component, Input, ChangeDetectionStrategy, OnChanges} from '@angular/core';


export class NglDatepickerWeekdays implements OnChanges {

   dayNamesShort: string[];
   dayNamesLong: string[];
   firstDayOfWeek: number;

  weekdays: any[] = [];

  ngOnChanges(changes?: any) {
    this.weekdays = [];
    for (let i = 0; i < 7; i++) {
      const offset = (this.firstDayOfWeek + i) % 7;
      this.weekdays.push({
        id: `weekday-${i}`,
        label: this.dayNamesShort[offset],
        title: this.dayNamesLong[offset],
      });
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'tr[nglWeekdays]',
  template: `
    <th *ngFor="let day of weekdays" [id]="day.id" scope="col"><abbr [title]="day.title">{{day.label}}</abbr></th>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'dayNamesShort': [{ type: Input },],
'dayNamesLong': [{ type: Input },],
'firstDayOfWeek': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
